@import './veriables.css';
header {
    width: 100%;
}

.navbar {
    display: flex !important;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    z-index: 99;
    background: var(--bg-card);
    width: 100%;
}

article {
    margin-top: 50px !important;
}

.navbar-nav li>span {
    position: relative;
    display: block;
    padding: 10px 15px;
    font-weight: bold;
}


/* adds some margin below the link sets  */

.navbar .dropdown-menu div[class*="col"] {
    margin-bottom: 1rem;
}

.navbar-light .navbar-nav .nav-link {
    font-size: 12px;
    color: var(--black-color);
    font-weight: 500;
}

.navbar .nav-item {
    display: flex;
    align-items: center;
}


/* breakpoint and up - mega dropdown styles */

@media screen and (min-width: 767px) {
    /* remove the padding from the navbar so the dropdown hover state is not broken */
    .navbar {
        padding-top: 0px;
        padding-bottom: 0px;
    }
    /* remove the padding from the nav-item and add some margin to give some breathing room on hovers */
    .navbar .nav-item {
        padding: .5rem .5rem;
        margin: 0 .25rem;
    }
    /* makes the dropdown full width  */
    .navbar .dropdown {
        position: static;
    }
    .navbar .dropdown-menu {
        width: 100%;
        left: 0;
        right: 0;
        /*  height of nav-item  */
        top: 60px;
        display: block;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s, opacity 0.3s linear;
        padding: 50px;
    }
    /* shows the dropdown menu on hover */
    .navbar .dropdown:hover .dropdown-menu,
    .navbar .dropdown .dropdown-menu:hover {
        display: block;
        visibility: visible;
        opacity: 1;
        transition: visibility 0s, opacity 0.3s linear;
    }
    .navbar .dropdown-menu {
        border: none;
        background-color: var(--lightBlueTwo-color);
    }
}

.dropdown-item.active,
.dropdown-item:active {
    background: none !important;
    color: #333 !important;
}

.dropdown-item.active,
.dropdown-item:hover {
    background: none !important;
    color: var(--primary-color);
    transition: ease .3s all;
}

.dropdown-item {
    margin: 12px 0 !important;
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link {
    color: var(--black-color) !important;
    background-color: var(--bg-card);
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
    color: var(--black-color) !important;
    transition: linear .3s all;
}

.dropdown-divider {
    border: none !important;
}

.navbar-light .navbar-toggler {
    border: none;
}

@media only screen and (max-width: 768px) {
    .menu-reverse {
        flex-direction: row-reverse;
    }
    .navbar-sign-app-container {
        display: none;
    }
    .navbar-brand img {
        width: 38px;
    }
    .dropdown-header {
        margin-top: 15px;
    }
}

.navbar-nav .dropdown-menu {
    position: absolute !important;
    left: -15px !important;
    width: 100vw !important;
    background-color: #f0f0f0 !important;
    border: none !important;
}

.dropdown-toggle::after {
    display: inline-block !important;
    width: .5rem !important;
    height: .5rem !important;
    border: 0.125rem solid !important;
    border-bottom: 0 !important;
    border-left: 0 !important;
    border-radius: 1px !important;
    text-indent: -624.9375rem !important;
    margin-left: 10px;
    -webkit-transform: rotate(135deg);
    -moz-transform: rotate(135deg);
    -ms-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    transform: rotate(135deg) translateY(0px);
    transition: linear .2s all;
}

.dropdown:hover>.dropdown-toggle::after {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    transition: linear .2s all;
}

.navbar-search {
    min-width: 280px;
    background-color: var(--gray-bg);
    padding: 10px 16px;
    border: solid 2px var(--gray-bg);
    border-radius: 30px;
    outline: none;
    display: flex;
    font-size: 12px;
}

::placeholder{
    color: var(--black-text) !important;
}

footer section .footer-in-email{
    background-color: var(--gray-bg);
}

.navbar-search input {
    border: none;
    outline: none;
    width: 100%;
    padding: 0 15px;
    background: none;
    color:  var(--black-text) !important;
}

.navbar-search i {
    font-size: 16px;
    color: #ccc;
}

.nav-dark-button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.5px solid #C7C7C7;
    width: 40px;
    height: 40px;
    background-color: #fff;
    color: #333;
    border-radius: 50%;
}

.nav-dark-button path{
    fill: var(--black-color) !important;
}